/* Landing */

.home-landing {
  width: 100%;
  height: calc(100vh - 100px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.home-landing img {
  width: 400px;
  height: auto;
}

.home-landing h3, h5 {
  color: #989899;
}

.footer {
  width: 100%;
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.social {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.social img {
  margin: 20px 5px 0 5px;
  max-height: 30px;
  width: auto;
  height: 100%;
}

.home-carousel {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
}

/* Objetivos */

.objetivos {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.obj-header {
  max-width: 800px;
  text-align: center;
  margin: 0 auto;
}

.obj-container {
  margin: 10px;
  display: flex;
  flex-wrap: wrap;
  flex-grow: 6;
  justify-content: center;
  align-content: center;
}

.obj-container img {
  margin: 5px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

/* Video */
video {
  width: auto;
  height: 600px;
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
}

.video-container {
  width: 100%;
  height: 100%;
}